<template>
  <div
    class="http_status"
    :style="is_remove ? 'display: none' : ''"
    v-loading="Loading"
  >
    <el-row align="middle" type="flex" class="name-id">
      <el-col :span="3"
        ><el-tooltip placement="bottom-start" class="name" :content="name">
          <div>{{ name }}</div>
        </el-tooltip>
        <div class="id no-line">{{ id }}</div></el-col
      >
      <el-col :span="6">
        <el-tooltip placement="bottom" class="url" content="点击复制">
          <div class="url-copy" :data-clipboard-text="url" @click="copy">
            {{ url }}
          </div>
        </el-tooltip>
      </el-col>
      <el-col :span="2"
        ><div class="status">
          <el-switch
            v-model="currentEnable"
            @click.native="switchEnable(currentEnable)"
            active-color="#13ce66"
            inactive-color="#ff4949"
          >
          </el-switch></div
      ></el-col>
      <el-col :span="8"
        ><div class="24h">
          <el-tooltip placement="bottom-end" class="status-24h-msg">
            <div slot="content">
              {{ status24_success }} / {{ status24_success + status24_fail }}
            </div>
            <el-progress
              :text-inside="true"
              :stroke-width="24"
              :percentage="current24Status"
              :status="current24StatusStr"
              :format="format"
              :style="current24StatusTextColor"
            ></el-progress>
          </el-tooltip></div
      ></el-col>
      <el-col :span="2"
        ><div class="rate">{{ rate }}分钟</div></el-col
      >
      <el-col :span="3"
        ><div class="setting">
          <el-popconfirm
            confirm-button-type="danger"
            confirm-button-text="好的"
            cancel-button-text="不用了"
            icon="el-icon-delete"
            icon-color="red"
            title="确定删除吗?"
            @confirm="remove"
          >
            <el-button
              slot="reference"
              type="danger"
              icon="el-icon-delete"
              circle
            ></el-button>
          </el-popconfirm></div
      ></el-col>
    </el-row>
  </div>
</template>
<script>
import Clipboard from "clipboard";
import axios from "axios";
import Qs from "qs";
export default {
  name: "http_status",
  props: {
    id: String,
    name: String,
    url: String,
    LastTime: String,
    enable: {
      type: Boolean,
      default: false,
    },
    status24: {
      type: Number,
      default: 98,
    },
    status24_fail: {
      type: Number,
      default: 2,
    },
    status24_success: {
      type: Number,
      default: 100,
    },
    rate: {
      type: Number,
      default: 15,
    },
  },
  data: function () {
    return {
      Loading: false,
      is_remove: false,
      currentEnable: this.enable,
      current24Status: this.status24,
      current24StatusStr:
        this.status24 >= 85
          ? "success"
          : this.status24 >= 25
          ? "warning"
          : "exception",
      current24StatusTextColor:
        this.status24 >= 30 ? "color: #fff;" : "color: #000",
    };
  },
  watch: {},
  methods: {
    copy() {
      let clipboard = new Clipboard(".url-copy");
      clipboard.on("success", () => {
        this.$notify.success({
          title: "成功",
          message: "复制成功",
        });
        clipboard.destroy();
      });
      clipboard.on("error", () => {
        this.$notify.error({
          title: "失败",
          message: "该浏览器不支持自动复制",
        });
        clipboard.destroy();
      });
    },
    format() {
      this.currentEnable = this.enable;
      this.current24Status = this.status24;
      this.current24StatusStr =
        this.status24 >= 85
          ? "success"
          : this.status24 >= 25
          ? "warning"
          : "exception";
      this.current24StatusTextColor =
        this.status24 >= 30 ? "color: #fff;" : "color: #000";
      return this.current24Status + "% (最后更新:" + this.LastTime + ")";
    },
    remove: function () {
      this.dialogVisible = false;
      this.is_remove = true;
      axios({
        method: "post",
        headers: { "Content-Type": "application/x-www-form-urlencoded" },
        url: window.host + "/http_monitor/remove",
        data: Qs.stringify({
          id: this.id,
        }),
      }).then(function () {});
    },
    switchEnable: function (val) {
      var self = this;
      this.Loading = true;
      axios({
        method: "post",
        headers: { "Content-Type": "application/x-www-form-urlencoded" },
        url: window.host + "/http_monitor/edit",
        data: Qs.stringify({
          id: this.id,
          key: "enable",
          value: val,
        }),
      }).then(function () {
        self.Loading = false;
        self.$notify({
          title: "成功",
          message: "状态修改成功",
          type: "success",
        });
      });
    },
  },
};
</script>

